<template>
    <Echart :options="options" :height="cdata.height" :width="cdata.width"></Echart>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler() {
                // const colors = this.cdata.colors
                this.options = {
                    yAxis: {
                        type: 'category',
                        data: this.cdata.yAxisData,
                    },
                    xAxis: this.cdata.xAxis,
                    series: [
                        {
                            data: this.cdata.seriesData,
                            type: 'bar',
                            showBackground: true,
                            backgroundStyle: {
                                color: 'rgba(180, 180, 180, 0.2)',
                            },
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

<style lang="scss" scoped></style>
