<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chartX.vue'
export default {
    data() {
        return {
            cdata: {
                height: '320px',
                width: '460px',
                xAxis: {
                    type: 'value',
                    name: '',
                    position: 'top',
                    alignTicks: true,
                    axisLine: {
                        show: true,
                        lineStyle: {
                            color: 'white',
                        },
                    },
                    axisLabel: {
                        formatter: '{value}',
                    },
                },
                yAxisData: ['单位1', '单位2', '单位3', '单位4', '单位5', '单位6'],
                seriesData: [120, 200, 150, 80, 70, 110],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random()*6) - 1;
                if (tmp1 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[tmp1]
                    cdata.seriesData[tmp1] = num + 5;
                    this.cdata = cdata;
                }
            }, 2000)
        }
    },
}
</script>

<style lang="scss" scoped></style>
