<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                height: '320px',
                width: '460px',
                legendData: ['预算', '预算执行', '预算执行率'],
                xAxisData: ['年度收款', '年度付款', '月度收款', '月度付款'],
                yAxisData: [
                    {
                        type: 'value',
                        name: '预算',
                        position: 'right',
                        alignTicks: true,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#4D73BE',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                    },
                    {
                        type: 'value',
                        name: '',
                        position: 'right',
                        alignTicks: true,
                        offset: 80,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#DF8244',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                    },
                    {
                        type: 'value',
                        name: '预算执行率',
                        position: 'left',
                        alignTicks: true,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#7EAA55',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}%',
                        },
                    },
                ],
                seriesData: [
                    {
                        name: '预算',
                        type: 'bar',
                        barWidth : 15,
                        data: [2.0, 4.9, 7.0, 23.2],
                    },
                    {
                        name: '预算执行',
                        type: 'bar',
                        yAxisIndex: 1,
                        barWidth : 15,
                        data: [2.6, 5.9, 9.0, 26.4],
                    },
                    {
                        name: '预算执行率',
                        type: 'line',
                        yAxisIndex: 2,
                        data: [2.0, 2.2, 3.3, 4.5],
                    },
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random()*3) - 1;
                let tmp2 = Math.ceil(Math.random()*4) - 1;

                if (tmp1 >= 0 && tmp2 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[tmp1].data[tmp2]
                    cdata.seriesData[tmp1].data[tmp2] = num + 1;
                    this.cdata = cdata;
                }
            }, 2000)
        }
    },
}
</script>

